.header {
    width: 100%;
    background-color: #fed700;
    padding: 12px 0;
  }
  
  .header__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 80px;
  }
  
  @media (max-width: 700px) {
    .header__wrapper {
      flex-direction: column;
      align-items: stretch;
      gap: 20px;
    }
  }
  